<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :isGetingItems="isGetingItems"
  >
    <v-layout column class="lay-body">
      <span class="txt-top">현재 비밀번호를 <br />입력해 주세요.</span>
      <c-input
        :hint="'비밀번호를 입력해주세요'"
        :value.sync="inputList.pw.value"
        class="mt-5"
        :type="'password'"
      />
      <v-spacer />
      <c-btn
        class="mt-5"
        :height="48"
        :thema="`default-bg${
          !validatePassword(inputList.pw.value) ? '-disable' : ''
        }`"
        :fontWeight="400"
        :fontSize="16"
        :readOnly="!validatePassword(inputList.pw.value)"
        :onClick="onClickActionNext"
        >다음</c-btn
      >
    </v-layout>
  </lay-header>
</template>

<script>
export default {
  middleware: "auth",
  data() {
    return {
      isGetingItems: false,
      inputList: {
        pw: {
          value: null,
          validation: () => {
            return this.checkPw();
          },
        },
      },
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    onClickActionNext() {
      for (let key in this.inputList) {
        if (this.inputList[key].validation) {
          if (!this.inputList[key].validation()) {
            return false;
          }
        }
      }
      this.pushRouter({
        name: "pass",
        params: {
          pass: this.inputList.pw.value,
          id: this.id,
        },
      });
    },
    checkPw() {
      if (
        !this.inputList.pw.value ||
        !this.validatePassword(this.inputList.pw.value)
      ) {
        return false;
      }
      return true;
    },
  },
};
</script>

